			   			<div class="usedHouse l">
			   				<h3>{{item.title}}</h3>
			   				<p>{{item.detail}}</p>
			   				<p><span class="toRed">{{item.price}}</span>{{item.detailPrice}}</p>
			   				<ul>
			   					<li class="l">{{item.feature01}}</li>
			   					<li class="l">{{item.feature02}}</li>
			   					<li class="l">{{item.feature03}}</li>
