   }
   .newList ul li i{
   	font-size: 12px;
   	padding: 0 2px;
   	color: gray;
   }
   /*newHouseList*/
  .newHouseList{
  	padding: 0 20px;
  	
  }
  .commonList{
  	padding-bottom: 18px;
  	border-bottom: 1px solid #e4e6f0;
  }
  .rentHouseOpen{
  	padding: 40px 0;
  	text-align: center;
  }
  .rentHouseOpen p:nth-child(1){
  	font-size:16px;
  	color: #666;
  	font-weight: bold;
  }
  .rentHouseOpen p:nth-child(2){
  	width: 20%;
  	padding: 5px 0;
  	border: 1px solid #00ae66;
    color: #00ae66;
    border-radius:2px ;
    margin-left: 40%;
    margin-top: 15px;
  }
</style>


